.wrapper {
    position: relative;
    width: 100%;
    height: 90%;

}
.map {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.fillData {
    position: absolute;
    top: 50px;
    left: 50px;
    width: 400px;
    height: 300px;
    background-color: white;
    padding: 30px 40px;
    font-size: 24px;
    font-weight: 700;
}
.myLink {
    padding: 5px 50px;
    background-color: #FDBF5A;
    border-radius: 20px;
    font-size: 23px;
    color: black;
}
.myLink:hover {
    background-color: #fab044;
}
.dialog {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px;
}
.dialogTitle {
    font-size: 18px;
    text-align: center;
    margin-bottom: 20px;
}
